.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.contentView{
  width: 100vw;
  height: 100vh;
  flex-direction: column;

  display: flex;
}


.viewbg{
  width: 100vw;
  height: 100vh;
  background: -moz-linear-gradient(top, #d0e475 0%, #62ffd5 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#d0e475), color-stop(100%,#62ffd5));
  background: -webkit-linear-gradient(top, #d0e475 0%,#62ffd5 100%);
  background: -o-linear-gradient(top, #d0e475 0%,#62ffd5 100%);
  background: -ms-linear-gradient(top, #d0e475 0%,#62ffd5 100%);
  background: linear-gradient(to bottom, #d0e475 0%,#62ffd5 100%);
  position: fixed;
  display: flex;
}

.Topheadline{
  width: 100%;
  height: 60px;
   color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
  font-family: Helvetica;
  font-weight: bold;
  padding-top: 60px;

}

.Subheadline{
  width: 100%;
  height: auto;
  color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1em;
  font-family: Helvetica;
  padding-bottom: 50px;


}
.popupView{
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;

}
.popupViewbg{
  width: 100%;
  height: 100%;
  opacity: 0.4;
  background-color: #282c34;
  position: absolute;

}
.themeyear{
  width: 15%;
  font-weight:bold;
  font-size: 18px;
  color: #333;
  font-family: "Heiti SC";

}
.themetext{
  width: 60%;
  height: auto;
  line-height: 20px;
  font-weight: normal;
  font-size: 14px;
  color: #333;
  font-family: "Heiti SC";


}
.popupcontent{
  width: 70%;
  height: 30%;
  min-height: 160px;
  min-width: 360px;
  background-color: #fff;
  position: absolute;
  border-radius: 8px;


}
.scrollcontent{
  width: 100%;
  height: 90%;
  overflow: scroll;


}
.Centerblock{
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;

}



.Linerblock{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
